import uniLoadMore from '@/components/uni-load-more.vue';
import server from '../../server.js';
export default {
	components: {
		uniLoadMore,
	},
	data() {
		return {
			TabCur:0,
			postList:[],
			hotpost:[],
			status: 'loading',
			Dindex:[],
			showAppFenxiang:false,
			type:"new",
			page:1,
			classinfo:[],
			statusTypes: [{
				value: 'more',
				text: '加载前',
				checked: true
			}, {
				value: 'loading',
				text: '加载中',
				checked: false
			}, {
				value: 'noMore',
				text: '我是有底线的',
				checked: false
			}],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
		}
	},
	onLoad:function(e){
		this.classId=e.id;
		this.getClassInfo(this.classId);
		this.getPostList(this.classId);
	},
	methods: {
		appFenxiang:function(id,type){
			switch (id){
				case 0:
					this.showAppFenxiang=false
					break;
				case 1:
					uni.setClipboardData({
						data: 'http://share.angeli.top/?postId='+this.postid,
						success: function () {
							console.log('success');
						}
					});
					console.log('复制链接')
					break;
				case 2:
					console.log(this.Dindex.PictureId[0])
					console.log(this.Dindex.Content)
					console.log('http://share.angeli.top/?postId='+this.Dindex.PostsId)
					uni.share({
					    provider: 'weixin',
					    scene: "WXSceneSession",
					    type: 5,
					    imageUrl: this.Dindex.PictureId[0],
					    title: this.Dindex.Content,
					    miniProgram: {
					        id: 'gh_a38adc10b952',
					        path: 'pages/postinfo/postinfo?id='+this.Dindex.PostsId,
					        type: 0,
					        webUrl: 'http://share.angeli.top/?postId='+this.Dindex.PostsId
					    },
					    success: ret => {
					        console.log(JSON.stringify(ret));
					    },
						fail: function (err) {
						    console.log("fail:" + JSON.stringify(err));
						}
					});
					break;
				case 3:
					uni.share({
					    provider: "weixin",
					    scene: "WXSenceTimeline",
					    type: 0,
					    href: 'http://share.angeli.top/?postId='+this.Dindex.PostsId,
					    title: this.Dindex.Content,
					    summary: this.Dindex.Content,
					    imageUrl: this.Dindex.PictureId[0],
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    },
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
					break;
				case 4:
					uni.share({
					    provider: "qq",
					    type: 1,
						href:'http://share.angeli.top/?postId='+this.Dindex.PostsId,
					    summary: this.Dindex.Content,
						title:this.Dindex.Content,
						imageUrl: this.Dindex.PictureId[0],
					    success: function (res) {
					        console.log("success:" + JSON.stringify(res));
					    }, 
					    fail: function (err) {
					        console.log("fail:" + JSON.stringify(err));
					    }
					});
					break;
				default:
					
			}
		},
		getPushPost:function(){
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.showToast({
					title: "你还没有登录，请登录后再来吧",
					position:'bottom',
					icon:'none'
				});
				setTimeout(function () {
					uni.navigateTo({
						url: '../reg/reg'
					})
				}, 1200);
			}else{
				server.postClass=this.classinfo;
				uni.navigateTo({
					url: '../post/post'
				})
			}
		},
		getClassInfo:function(e){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getClassInfo', //仅为示例，并非真实接口地址。
				data: {
					classId:e,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					uni.hideLoading();
					this.classinfo=[]
					this.classinfo=res.data.data;
					console.log(this.classinfo);
					this.weikong=false
					if(res.data.code!=="1"){
						uni.showToast({
							title: '获取话题信息失败',
							position:'bottom',
							icon:'none'
						});
					}
					this.$forceUpdate();
				}
			});
		},
		getpostinfo:function(id){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		},
		getbieren:function(e){
			if(e==server.userinfo.Auid){
				uni.navigateTo({
					url: '../i/i'
				})
			}else{
				uni.navigateTo({
					url: '../i/bieren?auid='+e
				})
			}
		},
		getPostList:function(classId){
			uni.showLoading({
				title: '获取数据中'
			});
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getClassPostList',
				data: {
					page: 1,
					count:20,
					classId:classId,
					postType:this.type,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					uni.hideLoading();
					this.postList=[]
					console.log("————————————帖子列表——————————");
					if(this.type=="new"){
						this.postList=res.data.data;
					}else{
						this.hotpost=res.data.data;
					}
					console.log(this.postList);
					this.weikong=false
					if(res.data.code!=="1"){
						uni.showToast({
							title: '获取帖子失败，建议重启',
							position:'bottom',
							icon:'none'
						});
					}
					if(res.data.data==false){
						this.weikong=true
					}
					//this.$forceUpdate();
				},
				complete:function(){
					uni.hideLoading();
				},
				
			});
		},
		showImage(res,c){
			var imageurl=res[c];
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['保存图片'],
					success: (resa) => {
						
						uni.downloadFile({
						    url: imageurl, 
						    success: (res) => {
								console.log(res)
						        if (res.statusCode === 200) {
						           uni.saveImageToPhotosAlbum({
									   filePath: res.tempFilePath,
									   success: function () {
										  uni.showToast({
										  	title: '已保存',
										  	position:'bottom',
										  	icon:'none'
										  });
									   }
								   });
						        }else{
									uni.showToast({
										title: '保存失败',
										position:'bottom',
										icon:'none'
									});
								}
						    }
						});
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		caidan:function(res){
			console.log(res)
			this.Dindex=res;
			if(res.AuthorId==server.userinfo.Auid){
				this.menuList=['分享给朋友', '举报','删除帖子']
			}else{
				this.menuList=['分享给朋友', '举报']
			}
			uni.showActionSheet({
				itemList:this.menuList,
				success: function (res) {
					switch(res.tapIndex){
						case 0:
							this.showAppFenxiang=true
							break;
							
						case 1:
							
							break;
						case 2:
							
							break;
						case 3:
						
							break;
						default:
							
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		EndLoding:function(e){
			this.page++;
			console.log(this.page)
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/post.php?type=getClassPostList',
				data: {
					page: 1,
					count:20,
					classId:classId,
					postType:this.type,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					uni.hideLoading();
					this.postList=[]
					console.log("————————————帖子列表——————————");
					if(this.type=="new"){
						this.postList=res.data.data;
					}else{
						this.hotpost=res.data.data;
					}
					console.log(this.postList);
					this.weikong=false
					if(res.data.code!=="1"){
						uni.showToast({
							title: '获取帖子失败，建议重启',
							position:'bottom',
							icon:'none'
						});
					}
					if(res.data.data==false){
						this.weikong=true
					}
					//this.$forceUpdate();
				},
				complete:function(){
					uni.hideLoading();
				},
				
			});
		},
		Like:function(postid,auid,give,index,zc){
			if(server.userinfo.Auid==""||server.userinfo.Auid==null){
				uni.showToast({
					title: "你还没有登录，请登录后再来吧",
					position:'bottom',
					icon:'none'
				});
				setTimeout(function () {
					uni.navigateTo({
						url: '../reg/reg'
					})
				}, 1200);
				return;
			}
			
			if(auid==server.userinfo.Auid){
				uni.showToast({
					title: "不能给自己种草",
					position:'bottom',
					icon:'none'
				});
				return;
			}
			
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", //仅为示例，并非真实接口地址。
				data: {
					fuid: auid,
					postid:postid,
					mode:modea,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'system':server.system
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							if(this.type=="new"){
								this.postList[index].ZhongcaoCount=Number(zc)+1;
								this.postList[index].Give=true;
							}else{
								this.hotpost[index].ZhongcaoCount=Number(zc)+1;
								this.hotpost[index].Give=true;
							}
							
							
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							if(this.type=="new"){
								this.postList[index].ZhongcaoCount=Number(zc)-1;
								this.postList[index].Give=false;
							}else{
								this.hotpost[index].ZhongcaoCount=Number(zc)-1;
								this.hotpost[index].Give=false;
							}
							
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		tabSelect(e) {
			this.TabCur = e
			if(e==0){
				this.type="new";
			}else{
				this.type="hot";
			}
			this.getPostList(this.classId);
			
		}
	}
}
